import React, {useEffect, useState} from 'react'
import { useLocation } from 'react-router'
import { Menu as UikitMenu } from '@pancakeswap/uikit'
import { languageList } from 'config/localization/languages'
import { useTranslation } from 'contexts/Localization'
import useTheme from 'hooks/useTheme'
import {useWeb3React} from "@web3-react/core";
import {ethers} from "ethers";
import config from './config/config'
import UserMenu from './UserMenu'
import GlobalSettings from './GlobalSettings'
import { getActiveMenuItem } from './utils'
import { footerLinks } from './config/footerConfig'
import { useChonk } from "../../hooks/useContract";

const Menu = (props) => {
  const { isDark, toggleTheme } = useTheme()
  const { currentLanguage, setLanguage, t } = useTranslation()
  const { pathname } = useLocation()
  const { account } = useWeb3React();
  const chonkContract = useChonk();
  const [chonkPrice, setChonkPrice] =useState(0)

  const activeMenuItem = getActiveMenuItem({ menuConfig: config(t), pathname })

  useEffect(() => {
    const getData = async () => {
      const promises = [chonkContract.buyPrice()];
      await Promise.all(promises).then((values) => {
        setChonkPrice(Number(ethers.utils.formatEther(BigInt(values[0]._hex))));
      });
    }
    if(account) {
      getData()
    }
  }, [account, chonkContract])

  return (
    <UikitMenu
      userMenu={<UserMenu />}
      globalMenu={<GlobalSettings />}
      isDark={isDark}
      toggleTheme={toggleTheme}
      currentLang={currentLanguage.code}
      langs={languageList}
      setLang={setLanguage}
      cakePriceUsd={chonkPrice}
      links={config(t)}
      footerLinks={footerLinks(t)}
      activeItem={activeMenuItem?.href}
      buyCakeLabel={t('Buy CHONK')}
      {...props}
    />
  )
}

export default Menu
